import React, { useEffect } from 'react';
import { message, Segmented } from 'antd';
import ScrollView from '@/components/ScrollView';
import { uuid, isLightColor, copy } from '@/utils/tool';
import { useColorStore } from '@/stores';
import { type ColorBaseParam } from '@/stores/slices/color';
import colorMap from '@/module/constants/colorMap';
import styles from './index.less';

const Choose = () => {
  const setColor = useColorStore((state) => state.setColor);
  const curData = useColorStore((state) => state.curData);
  const setCurData = useColorStore((state) => state.setCurData);

  const handleClick = (item: ColorBaseParam) => {
    setColor(item);
    copy(item.hex, () => {
      message.destroy();
      message.success(`${item.name} 复制: ${item.hex}`);
    });
  };
  useEffect(() => {
    setCurData(colorMap.zhongguose);
    setColor(colorMap.zhongguose.list[0]);
  }, []);

  const segmentedOptions: any[] = [];
  Object.keys(colorMap).forEach(key => {
    const item = (colorMap as any)[key];
    segmentedOptions.push({ label: item.label, value: item.value });
  });

  const handleChange = (value: any) => {
    const item = (colorMap as any)[value];
    setCurData(item);
    setColor(item.list[0]);
  };
  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <Segmented
          options={segmentedOptions}
          value={curData?.value || colorMap.zhongguose.value}
          onChange={handleChange}
        />
      </div>
      <div className={styles.content}>
        <ScrollView autoHeight>
          <div className={styles.list}>
            {(curData?.list || []).map(i => (
              <div key={uuid()} className={styles.wrap}>
                <div
                  className={styles.item}
                  style={{ background: i.hex, color: isLightColor(i.hex) ? '#000' : '#fff' }}
                  onClick={() => handleClick(i)}
                >
                  <span className={styles.title}>{i.name}</span>
                  <span className={styles.value}>{i.hex}</span>
                </div>
              </div>
            ))}
          </div>
        </ScrollView>
      </div>
    </div>
  );
};

export default Choose;
