import React from 'react';
import { Progress } from 'antd';
import { isLightColor } from '@/utils/tool';
import { useColorStore } from '@/stores';
import styles from './index.less';

const Effect = () => {
  const color = useColorStore((state) => state.color);
  if (!color) { return null; }
  const _isLightColor = isLightColor(color.hex);
  const textColor = _isLightColor ? '#000' : '#fff';
  const trailColor = _isLightColor ? 'rgba(0,0,0,0.25)' : 'rgba(255, 255, 255, 0.25)';
  const cmykColor = ['#0093D3', '#CC006B', '#FFF10C', '#333333'];
  return (
    <div className={styles.container}>
      <div className={styles.card} style={{ background: color.hex, color: textColor }}>
        <div className={styles.descOne}>
          <div className={styles.cmyk}>
            {color.cmyk.map((o, index) => (
              <div key={`cmyk-${index}`} className={styles.progress}>
                <sub>{'CMYK'[index]}</sub>
                <Progress
                  type="circle"
                  percent={+(o * 100).toFixed()}
                  size={40}
                  trailColor={trailColor}
                  strokeColor={cmykColor[index]}
                  format={(percent) => percent} />
              </div>
            ))}
          </div>
        </div>
        <div className={styles.descTwo}>
          <div className={styles.info}>
            <div className={styles.pinyin}>{color.pinyin || ''}</div>
            <div className={styles.hex}>{color.hex}</div>
          </div>
          <div className={styles.rgb}>
            {color.rgb.map((o, index) => (
              <div key={`rgb-${index}`} className={styles.progress}>
                <span className={styles.label}>{'RGB'[index]}</span>
                <Progress
                  percent={+(o / 255 * 100).toFixed()}
                  size='small'
                  trailColor={trailColor}
                  strokeColor={textColor}
                  format={(percent) => o} />
              </div>
            ))}
          </div>
        </div>
        <div className={styles.descThree}>
          <div className={styles.name}>{color.name}</div>
        </div>
      </div>
    </div>
  );
};

export default Effect;
