import { StateCreator } from 'zustand';
import { hex2Cmyk, hex2Rgb } from '@/utils/tool';

export interface ColorBaseParam {
  name: string;
  hex: string;
  pinyin?: string;
}

interface ColorParam extends ColorBaseParam {
  rgb: number[];
  cmyk: number[];
}

interface CurDataParam {
  label: string;
  value: string;
  list: ColorBaseParam[];
}

export interface ColorSlice {
  curData: CurDataParam | undefined;
  color: ColorParam | undefined;
  setColor: (item: ColorBaseParam) => void;
  setCurData: (curData: CurDataParam) => void;
};

export const createColorSlice: StateCreator<ColorSlice> = (set) => ({
  curData: undefined,
  color: undefined,
  setColor: (item: ColorBaseParam) => {
    set(() => ({
      color: {
        name: item.name,
        hex: item.hex,
        pinyin: item.pinyin,
        rgb: hex2Rgb(item.hex),
        cmyk: hex2Cmyk(item.hex),
      }
    }));
  },
  setCurData: (curData: CurDataParam) => set({ curData }),
});
