/**
 * 生成随机uuid
 */
export const uuid = () => {
  return (
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)
  );
};

/**
 * hex转rgb
 * return [r, g, b]
 */
export const hex2Rgb = (hex: string) => {
  let color = hex.toLowerCase();
  const reg = /^#([0-9a-fA-F]{3}|[0-9a-fA-F]{6})$/;
  if (reg.test(color)) {
    color = color.replace(/#/, '');
    if (color.length === 3) {
      let tempHex = '';
      for (let i = 0; i < color.length; i++) {
        tempHex += color[i] + color[i];
      }
      color = tempHex;
    }
    const r = parseInt(color.substring(0, 2), 16);
    const g = parseInt(color.substring(2, 4), 16);
    const b = parseInt(color.substring(4, 6), 16);
    return [r, g, b];
  }
  return [0, 0, 0];
};

/**
 * 获取cmyk值
 * return [c, m, y, k]
 */
export const hex2Cmyk = (hex: string) => {
  const [r, g, b] = hex2Rgb(hex);
  let computedC = 1 - (r / 255);
  let computedM = 1 - (g / 255);
  let computedY = 1 - (b / 255);
  let minCMY = Math.min(computedC, Math.min(computedM, computedY));
  computedC = (computedC - minCMY) / (1 - minCMY);
  computedM = (computedM - minCMY) / (1 - minCMY);
  computedY = (computedY - minCMY) / (1 - minCMY);
  const computedK = minCMY;
  return [computedC, computedM, computedY, computedK];
};

/**
 * 判断颜色深浅
 * @returns boolean
 */
export const isLightColor = (hex: string) => {
  const colors = hex2Rgb(hex);
  return +colors[0] * 0.299 + +colors[1] * 0.587 + +colors[2] * 0.114 >= 192;
};

/**
  * 复制
  */
export const copy = (value: string, callback?: () => void) => {
  let copyTextArea = null;
  try {
    copyTextArea = document.createElement('textarea');
    copyTextArea.style.height = '0px';
    copyTextArea.style.width = '0px';
    copyTextArea.style.opacity = '0';
    document.body.appendChild(copyTextArea);
    copyTextArea.value = value;
    copyTextArea.select();
    document.execCommand('copy');
    // eslint-disable-next-line no-unused-expressions
    callback && callback();
  } finally {
    if (copyTextArea && copyTextArea.parentNode) {
      copyTextArea.parentNode.removeChild(copyTextArea);
    }
  }
};
